
body {
  font-family: sans-serif;
}
.simple {
  background-color: #E8E8E8;
  color: #2b2b2b;
}
.special {
  background-color: #74C773;
  color: #fff;
}
.heroic {
  background-color: #3DA4CF;
  color: #fff;
}
.legendary {
  background-color: #8C8CEA;
  color: #fff;
}
.mythic {
  background-color: #85DDC9;
  color: #fff;
}
.familiars {
  background-color: #85877C;
  color: #fff;
}
.button {
  border: none;
  margin: .2em;
  padding: .6em 1em;
}
.button:hover {
    cursor: pointer;
}
.button:focus {
    outline: none;
}
.spacer {
  margin-top: .4em;
}
.card-root-container {
  padding-left: 3.5em;
}
.card-root-container .side-nav {
    background-color: #D3B326;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    box-sizing: border-box;
    width: 3.5em;
}
.card-root-container .side-nav .top-icon {
      display: block;
      background-color: #2b2b2b;
      width: 100%;
      height: 3.5em;
      box-sizing: border-box;
      padding: 1.125em;
      border-right: 1px solid #454545;
}
.card-root-container .side-nav .top-icon .icon-box {
        width: .75em;
        height: .75em;
        position: absolute;
        border-color: #fff;
        border-style: solid;
        border-width: 3px;
}
.card-root-container .side-nav .top-icon .one {
        top: 0.75em;
        left: 0.75em;
        border-left: none;
        border-top: none;
}
.card-root-container .side-nav .top-icon .two {
        top: 0.75em;
        left: 1.875em;
        border-top: none;
        border-right: none;
}
.card-root-container .side-nav .top-icon .three {
        top: 1.875em;
        left: 0.75em;
        border-left: none;
        border-bottom: none;
}
.card-root-container .side-nav .top-icon .four {
        top: 1.875em;
        left: 1.875em;
        border-bottom: none;
        border-right: none;
}
.card-root-container .side-nav .nav-item {
      display: block;
      color: #fff;
      margin-top: 2em;
      text-decoration: none;
      font-size: .75rem;
      text-align: center;
      padding: .75em 0em 1.8em;
      font-weight: 600;
}
.card-root-container .side-nav .nav-item:hover {
        background-color: #dfc44d;
}
.card-root-container .side-nav .nav-item:hover > .nav-item-name {
        display: block;
}
.card-root-container .side-nav .nav-item img {
        width: 3em;
}
.card-root-container .side-nav .nav-item .nav-item-name {
        display: none;
        position: absolute;
        text-align: center;
        width: 4.75em;
        left: 0px;
}



