
body {
  font-family: sans-serif;
}
.simple {
  background-color: #E8E8E8;
  color: #2b2b2b;
}
.special {
  background-color: #74C773;
  color: #fff;
}
.heroic {
  background-color: #3DA4CF;
  color: #fff;
}
.legendary {
  background-color: #8C8CEA;
  color: #fff;
}
.mythic {
  background-color: #85DDC9;
  color: #fff;
}
.familiars {
  background-color: #85877C;
  color: #fff;
}
.button, .card-search-container .card-filter .filter-buttons .rarity-button {
  border: none;
  margin: .2em;
  padding: .6em 1em;
}
.button:hover, .card-search-container .card-filter .filter-buttons .rarity-button:hover {
    cursor: pointer;
}
.button:focus, .card-search-container .card-filter .filter-buttons .rarity-button:focus {
    outline: none;
}
.spacer {
  margin-top: .4em;
}
.card-search-container {
  display: inline-block;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
  width: 100%;
  padding-bottom: 10em;
}
.card-search-container .card-list-container {
    width: 100%;
    background-color: #2b2b2b;
    height: 100%;
    padding: .65em;
    overflow-y: scroll;
    overflow-x: hidden;
    box-sizing: border-box;
}
@media screen and (max-width: 1024px) {
.card-search-container .card-list-container {
        padding: .25em;
}
}
.card-search-container .card-filter {
    width: 100%;
    top: 0px;
    left: 0px;
    background-color: #2b2b2b;
    z-index: 9999;
    padding: 1em;
    box-sizing: border-box;
    border-bottom: solid 2px #eee;
}
.card-search-container .card-filter h3 {
      color: #fff;
      font-family: sans-serif;
      font-weight: 700;
      margin: 0em 0em .35em 0em;
}
.card-search-container .card-filter .search-container {
      display: inline-block;
      width: 100%;
      margin-left: 0em;
      box-sizing: border-box;
}
.card-search-container .card-filter .search-container input {
        padding: .75em;
        height: 3em;
        box-sizing: border-box;
        border: none;
        border-radius: 4px;
        width: 100%;
}
.card-search-container .card-filter .search-container input:focus {
          outline: none;
}
.card-search-container .card-filter .filter-buttons {
      margin-top: .5em;
}



