
body {
  font-family: sans-serif;
}
.simple {
  background-color: #E8E8E8;
  color: #2b2b2b;
}
.special {
  background-color: #74C773;
  color: #fff;
}
.heroic {
  background-color: #3DA4CF;
  color: #fff;
}
.legendary {
  background-color: #8C8CEA;
  color: #fff;
}
.mythic {
  background-color: #85DDC9;
  color: #fff;
}
.familiars {
  background-color: #85877C;
  color: #fff;
}
.button {
  border: none;
  margin: .2em;
  padding: .6em 1em;
}
.button:hover {
    cursor: pointer;
}
.button:focus {
    outline: none;
}
.spacer {
  margin-top: .4em;
}
h1 {
  margin: .75em 0em .25em .5em;
}
.simple-cards {
  color: #9c9c9c;
}
.special-cards {
  color: #74C773;
}
.heroic-cards {
  color: #3DA4CF;
}
.legendary-cards {
  color: #8C8CEA;
}
.mythic-cards {
  color: #85DDC9;
}



